<template>
  <main class="h-100">
    <div class="container h-100">
      <div class="justify-content-center pt-2" id="js-title">
        <h2 class="text-center">ALTAMENTE ADMIN PORTAL</h2>
      </div>

      <nav>
        <div class="nav nav-tabs px-2" id="nav-tab" role="tablist">
          <a class="nav-item my-link nav-link text-muted border border-bottom-0 active" id="nav-log-tab" data-toggle="tab" href="#nav-log" role="tab" aria-controls="nav-log" aria-selected="true">System log</a>
          <a class="nav-item my-link nav-link text-muted border border-bottom-0" id="nav-links-tab" data-toggle="tab" href="#nav-links" role="tab" aria-controls="nav-links" aria-selected="false">Links</a>
          <a class="nav-item my-link nav-link text-muted border border-bottom-0" id="nav-contact-tab" data-toggle="tab" href="#nav-contact" role="tab" aria-controls="nav-contact" aria-selected="false">Contact</a>
        </div>
      </nav>

      <div class="tab-content shadow-lg rounded" id="nav-tabContent">
        <div class="tab-pane p-4 fade show active" id="nav-log" role="tabpanel" aria-labelledby="nav-log-tab">
          <div class="noactive">
            <h4 id="js-title-tab">Live System Event Log</h4>
            <p>
              <live-log />
            </p>
          </div>
        </div>
        <div class="tab-pane p-4 fade" id="nav-links" role="tabpanel" aria-labelledby="nav-links-tab">
          <div class="noactive">
            <h4 id="js-title-tab">Links to other resources</h4>
              <Links-Tab />
          </div>
        </div>
        <div class="tab-pane p-4 fade" id="nav-contact" role="tabpanel" aria-labelledby="nav-contact-tab">
          <div class="noactive">
            <h4 id="js-title-tab"> Lorem ipsum dolor </h4>
            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Sit non, soluta veniam error commodi iste blanditiis, laborum saepe ex
            nisi architecto eligendi quos, vel cum numquam quidem et eos voluptate.
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import $ from 'jquery';
import LiveLog from './components/LiveLog.vue'
import LinksTab from './components/LinksTab.vue'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'

export default {
  name: 'app',
  components: {
    LiveLog,
    LinksTab
  }
}

/* adaptive height of terminal and other tabs*/

function heightTerm(){
  //height of tab
	var hFirstDiv = $("#nav-tab").height(); //height of 1st external element
  var hSecondDiv = $("#js-title").height(); //height of 2nd external element
  var sumPadding = 10; //The sum of all paddings
  var hPerent = (( $(window).height() ) - (hFirstDiv + hSecondDiv + sumPadding)) * 0.95;
  $("#nav-log").css("height", "" + hPerent + "");
  //height of tab-content
  var hTitleTab = 36; //height of the 1st elements in the parent + margin
  var usableHeight = (($("#nav-log").height()) - hTitleTab);
  $("#log").css("height", "" + usableHeight + "");
}

/* adaptive height of tab Links*/ 

function heightLinks() {
  //height of tab
  var hFirstDiv = $("#nav-tab").height(); //height of 1st external element
  var hSecondDiv = $("#js-title").height(); //height of 2nd external element
  var sumPadding = 10; //The sum of all paddings
  var hPerent = (( $(window).height() ) - (hFirstDiv + hSecondDiv + sumPadding)) * 0.95;
  $("#nav-links").attr('style','min-height:' + hPerent + 'px');
  //height of tab-content
  var hTitleTab = 36; //height of the 1st elements in the parent + margin
  var usableHeight = (($("#nav-log").height()) - hTitleTab);
  $("#link").attr('style','min-height:' + usableHeight + 'px');
}

$(document).ready(function (){ 

  heightTerm();
  $("#nav-log-tab").click(function(){ heightTerm(); });
  $("#nav-links-tab").click(function(){ heightLinks(); });
  
  $(window).resize(function() { 
    heightTerm();
    heightLinks();
  })
});

</script>

<style>
body {
  font: 12px/20px 'Lucida Grande', Verdana, sans-serif;
}

.active{
  color: black !important;
  font-weight: bold;
}

.noactive{
  color: inherit;
  font-weight: normal;
}
</style>