<template>

    <div id="log" class="overflow-auto rounded"></div>

</template>


<script>

import $ from 'jquery';
import * as signalR from '@aspnet/signalr/'
import 'jquery.terminal'
import 'jquery.terminal/css/jquery.terminal.min.css'

export default {
  name: 'LiveLog',
  props: {
    msg: String
  },
  mounted() {
    var term;
    term = $('#log').terminal(function (command) {

      this.echo('');
    }, {
      greetings: 'System log',
      name: 'syslog',
      prompt: ''
    });

    const connection = new signalR.HubConnectionBuilder()
      .withUrl(process.env.VUE_APP_LOGHUB_URL + "/LogHub?loglevels=all", { skipNegotiation: true, transport: signalR.HttpTransportType.WebSockets })
      .configureLogging(signalR.LogLevel.Information)
      .build();

    async function start() {
      try {
        await connection.start();
        console.log("connected");
      } catch (err) {
        console.log(err);
        setTimeout(() => start(), 5000);
      }
    };

    connection.onclose((error) => {
      setTimeout(() => start(), 5000);
    });



    // connection.on("sendLogEvent", (message) => {
    //   //2005-03-19 15:10:26,618 - simple_example - DEBUG - debug message

    //   term.echo(`${new Date(message["@@t"]).toLocaleString()} - ${message.service} - ${message["@@l"] || "Information"} - ${message["@@m"]}`);

    // });


    connection.on("sendLogEvent", (message) => {
      //term.echo(message.timestamp + ' : ' + message.properties.service + ' : ' + message.messageTemplate);
      // debugger

      /* Changing Colors */
      var color;

      if (typeof message["@l"] != "undefined") {
        
        if (message["@l"].includes('Information')) {
          color = "[[!;royalblue;]";
          } else if (message["@l"].includes('Warning')) {
            color = "[[!;orange;]";
          } else if (message["@l"].includes('Debug')) {
            color = "[[!;white;]";
          } else if (message["@l"].includes('Error')) {
            color = "[[!;red;]";
          } else if (message["@l"].includes('Fatal')) {
            color = "[[!b;darkred;]";
        } 
        
      } else {
          color = "[[!;royalblue;]";
        }
      
      term.echo(color + new Date(message["@t"]).toLocaleString() + ' : ' + message.service + ' : ' +  (message["@l"] || "Information") + ' - ' +  message["@m"] + ']');
    });

    start();

  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scope>

  #log {
    width: auto;
  }

</style>
